	module.exports = {"data":{"markdownRemark":{"html":"<h2>Visual Design</h2>\n<p>In this section, we’ll have guides to a wide variety of visual design concepts.</p>\n<p>Visual Design deals with how applications look. It can be seen as the digital equivalent of graphic design. And as such, visual design should also strive to follow Dieter Rams’ well-known principles of good design.</p>\n<h3>10 Principles of Good Design</h3>\n<ol>\n<li>Good design is innovative</li>\n<li>Good design makes a product useful</li>\n<li>Good design is aesthetic</li>\n<li>Good design makes a product understandable</li>\n<li>Good design is unobtrusive</li>\n<li>Good design is honest</li>\n<li>Good design is long-lasting</li>\n<li>Good design is thorough down to the last detail</li>\n<li>Good design is environmentally friendly</li>\n<li>Good design involves as little design as possible</li>\n</ol>\n<p>Various design methodoligies can be employed. Different companies have their own design languages in a broad sense. For example, Google is a large propogator of Material Design in which design elements are made to look like real materials with the same flow and shadow effects.</p>","fields":{"slug":"/visual-design/"},"frontmatter":{"title":"Visual Design"}}},"pathContext":{"slug":"/visual-design/"}}

